import React from 'react';
import gsap from 'gsap';
import { ScrollToPlugin } from 'gsap/ScrollToPlugin';

//  GSAP  ScrollToPlugin
gsap.registerPlugin(ScrollToPlugin);

const Footer = () => {
  //    
  const handleSmoothScroll = (e, targetId) => {
    e.preventDefault(); //    

    //  GSAP   
    gsap.to(window, {
      duration: 1, //    
      scrollTo: { y: targetId, offsetY: 50 }, //       
      ease: 'power2.inOut', //  easing
    });
  };

  return (
    <footer id="contact" style={styles.footer}>
      {/*    () */}
      <div style={styles.sectionLeft}>
        <h2 style={styles.heading}>Contacts</h2>
        <ul style={styles.links}>
          <li>
            <a
              href="https://github.com/makarenkoooooo"
              style={styles.link}
              target="_blank"
              rel="noopener noreferrer"
            >
              GitHub
            </a>
          </li>
          <li>
            <a href="mailto:dodiverinwork@gmail.com" style={styles.link}>
              Email
            </a>
          </li>
          <li>
            <a
              href="https://t.me/makarenko99"
              style={styles.link}
              target="_blank"
              rel="noopener noreferrer"
            >
              Telegram
            </a>
          </li>
        </ul>
      </div>

      {/*    () */}
      <div style={styles.sectionRight}>
        <h2 style={styles.heading}>Menu</h2>
        <ul style={styles.links}>
          <li>
            <a
              href="#aboutMe"
              onClick={(e) => handleSmoothScroll(e, '#aboutMe')}
              style={styles.link}
            >
              About me
            </a>
          </li>
          <li>
            <a
              href="#skills"
              onClick={(e) => handleSmoothScroll(e, '#skills')}
              style={styles.link}
            >
              Skills
            </a>
          </li>
          <li>
            <a
              href="#projects"
              onClick={(e) => handleSmoothScroll(e, '#projects')}
              style={styles.link}
            >
              Projects
            </a>
          </li>
          <li>
            <a
              href="#experience"
              onClick={(e) => handleSmoothScroll(e, '#experience')}
              style={styles.link}
            >
              Experience
            </a>
          </li>
        </ul>
      </div>

      {/*    (  ) */}
      <div style={styles.copyright}>
        <p> 2025 Makaronka</p>
      </div>
    </footer>
  );
};

const styles = {
  footer: {
    backgroundColor: '#000', //  
    color: '#fff', //  
    display: 'flex',
    flexDirection: 'column', //  
    justifyContent: 'space-between', //   
    alignItems: 'center',
    padding: '40px',
  },
  sectionLeft: {
    flex: 1,
    textAlign: 'left',
    alignSelf: 'flex-start',
    paddingLeft: '40px',
  },
  sectionRight: {
    flex: 1,
    textAlign: 'right',
    alignSelf: 'flex-end',
    paddingRight: '40px',
  },
  heading: {
    fontSize: '36px', //   
    marginBottom: '20px',
    fontWeight: 'bold',
  },
  links: {
    listStyle: 'none',
    padding: 0,
    margin: 0,
  },
  link: {
    color: '#fff',
    textDecoration: 'none',
    fontSize: '24px', //    
    margin: '15px 0',
    display: 'block',
    transition: 'color 0.3s ease', //   
  },
  linkHover: {
    color: '#aaa', //    
  },
  copyright: {
    marginTop: 'auto', //  
    textAlign: 'center',
    fontSize: '16px',
    color: '#aaa', //   
    paddingTop: '20px',
  },
};

export default Footer;
