import React, { useEffect, useRef } from 'react';
import gsap from 'gsap';

const Main = () => {
  const nameRef = useRef(null); //     
  const titleRef = useRef(null); //   -
  const buttonRef = useRef(null); //    "About Me"

  useEffect(() => {
    //        
    gsap.fromTo(
      nameRef.current,
      {
        opacity: 0,
        y: '100vh',
      },
      {
        opacity: 1,
        y: 0,
        duration: 1.5,
        ease: 'power4.out',
      }
    );

    //  -  
    gsap.fromTo(
      titleRef.current,
      {
        opacity: 0,
        y: '100vh',
      },
      {
        opacity: 1,
        y: 0,
        duration: 1.5,
        ease: 'power4.out',
        delay: 1.0,
      }
    );

    //    
    gsap.fromTo(
      buttonRef.current,
      {
        opacity: 0,
        y: '50px',
      },
      {
        opacity: 1,
        y: 0,
        duration: 1.5,
        ease: 'power4.out',
        delay: 2.0,
      }
    );
  }, []);

  const scrollToAboutMe = () => {
    document.getElementById('aboutMe').scrollIntoView({ behavior: 'smooth' });
  };

  return (
    <div className="main">
      <h1 ref={nameRef} className="main-title">
        SERGEY DODIVERIN
      </h1>
      <h2 ref={titleRef} className="main-subtitle">
        Frontend Developer
      </h2>

      {/*        */}
      <button
        ref={buttonRef}
        className="about-me-btn"
        onClick={scrollToAboutMe}
      >
        About Me
      </button>
    </div>
  );
};

export default Main;
